<template>
    <div>
        <div class="d-flex pa-4" style="height:54px;align-items: center;">
            <div class="mr-4 font-weight-medium">標記狀況 </div>
            <v-divider class="mr-4" vertical></v-divider>
            <v-breadcrumbs :items="breadcrumbItems" class="pa-0">
                <template v-slot:divider>
                   —
                </template>
            </v-breadcrumbs>
            <v-spacer></v-spacer>
            <v-btn depressed style="background: #F5F8FA;color:#7E8299">返回</v-btn>
            <v-btn depressed class="ml-4" style="background: #6291F4;color:#ffffff" >建立標記設定</v-btn>
        </div>
        <div style="background: #FAFCFE;" class="pa-4">
            <h2 class="mb-4">離婚後親權裁判預測專案</h2>
            <v-data-table
                :headers="headers"
                :items="desserts"
                :page.sync="page"
                hide-default-footer
                class="elevation-0 project_table"
                @page-count="pageCount = $event"
            >
                <template v-slot:item.name="{item}">
                    <router-link to="/" @click.native="goVerdictDetail(item)">{{item.name}}</router-link>
                </template>
                <template v-slot:item.delete>
                    <v-btn @click="deleteItem=true" class="mx-1 pa-0" style="min-wight:30px;color:#F1416C" color="#FFF5F8" small depressed>
                        <v-icon dark>
                            mdi-trash-can
                        </v-icon>
                    </v-btn>
                </template>
            </v-data-table>
            <div class="d-flex mt-4 align-center">
                <span>Showing 1 to 10 of 50 entries</span>
                <v-spacer></v-spacer>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                ></v-pagination>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data: vm => ({
            projectId: 1,
            pageCount: 10,
            page: 1,
            dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
            date: "",
            menu2: false,
            headers: [{
                text: '名稱',
                align: 'start',
                sortable: false,
                value: 'name'
            },
            { text: '進度', value: 'schedule' },
            { text: '檢查', value: 'check' },
            { text: '標記者', value: 'markers' },
            { text: '標記時間', value: 'markTime'},
            { text: '刪除', value: 'delete',align: 'center' }],
            desserts: [{
                id: 1,
                name: "法務部 法律字第10603504060號",
                schedule: 100,
                check: false,
                markers: ["test1"],
                markTime: "2021-11-11"
            }],
            breadcrumbItems: [{
                text: '我的專案',
                disabled: false,
                to: "/projects",
                exact: true
            }, {
                text: '標記狀況',
                disabled: true
            }]
        }
    ),
    computed: {
      computedDateFormatted () {
        return this.formatDate(this.date)
      },
    },
    watch: {
      date () {
        this.dateFormatted = this.formatDate(this.date)
      },
    },
    methods: {
        markSetting(item) {
            this.$router.push(`/mark-setting/${item.id}`)
        },
        goVerdicts(item) {
            this.$router.push(`/projects/${item.id}/verdicts`)
        },
        formatDate (date) {
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${year}/${month}/${day}`
        },
        parseDate (date) {
            if (!date) return null

            const [month, day, year] = date.split('/')
            return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
        },
        goVerdictDetail(item) {
            this.$router.push(`/projects/${item.id}/verdicts/${item.id}`)
        }
    }
}
</script>
<style lang="scss">
.project_table {
    .v-data-table-header {
        background: #EFF4FF;
        color: #3F4254;
    }
}
